.control-area-container {
  position: absolute;
  top: 170px;
  right: 20px;
  width: 400px;
  height: 400px;
  box-shadow: 0 0 15px rgba(0, 0, 0, .32);
  background: url(./images/bg.png) no-repeat center / 100% 100%;
  .control-area-title {
    display: flex;
    align-items: center;
    width: 264px;
    height: 48px;
    margin-top: 28px;
    background: url(./images/titleBg.png) no-repeat center / 100% 100%;
    .label {
      color: #fff;
      font-size: 18px;
      margin-left: 58px;
    }
    .num {
      font-size: 30px;
      color: #0aff55;
      text-shadow: 0 0 15px #0aff55;
      margin-left: 24px;
    }
  }
}

.control-area-wrapper {
  overflow: hidden;
  .control-area-group {
    position: relative;
    width: 358px;
    height: 290px;
    margin: 14px auto 0 auto;
    .control-area-item {
      cursor: pointer;
      width: 100%;
      height: 88px;
      margin-top: 12px;
      padding: 12px 16px;
      box-sizing: border-box;
      background: url(./images/listBg.png) no-repeat center / 100% 100%;
      .control-area-item-title {
        display: flex;
        align-items: center;
        span {
          width: 14px;
          height: 4px;
          background: #76ffff;
          box-shadow: 0 0 10px #76ffff;
        }
        p {
          font-size: 18px;
          color: #fff;
          margin-left: 6px;
        }
      }
      .control-area-item-content {
        width: 320px;
        margin-top: 8px;
        font-size: 16px;
        line-height: 20px;
        color: #5dc1ff;
        overflow : hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
      }
    }
    .control-area-item:nth-child(1) {
      margin-top: 0;
    }
  }
}


