.suspicious-vehicle{
  .suspicious-title{
    position: relative;
    height: 50px;
    width: 265px;
    line-height: 40px;
    color: #fff;
    font-size: 20px;
    text-indent: 60px;
    background: url(../images/title-bg.png) no-repeat 0 0/contain;
    &:before{
      content: '';
      position: absolute;
      top: 8px;
      left: 20px;
      width: 28px;
      height: 28px;
      background: url(../images/vehicle-icon.png) no-repeat 0 0/100% 100%;
    }
    .value{
      margin-left: 10px;
      font-size: 30px;
      color: #0aff55;
    }
  }
  .list-container{
    box-sizing: border-box;
    padding: 5px;
    width: 100%;
    height: 160px;
    .prev{
      float: left;
      cursor: pointer;
      width: 28px;
      height: 28px;
      margin-top: 50px;
      background: url(../images/prev.png) no-repeat 0 0/100% 100%;
    }
    .next{
      float: right;
      cursor: pointer;
      right: 0px;
      width: 28px;
      height: 28px;
      margin-top: 50px;
      background: url(../images/next.png) no-repeat 0 0/100% 100%;
    }
    .list{
      position: relative;
      overflow: hidden;
      width: 270px;
      height: 100%;
      margin: 0 auto;
      white-space: nowrap;
      ul{
        position: absolute;
        font-size: 0;
        li{
          list-style: none;
          display: inline-block;
          width: 82px;
          margin-left: 4px;
          margin-right: 4px;
          font-size: 0;
          .photo{
            display: block;
            height: 110px;
            width: 100%;
            margin: 0 auto;
          }
          .name{
            margin-top: 5px;
            height: 30px;
            width: 100%;
            line-height: 30px;
            font-size: 16px;
            color: #fff;
            text-align: center;
            background: url(../images/name-bg.png) no-repeat 0 0/100% 100%;
          }
        }
      }
    }
  }
}


