.video-plate-container {
  position: absolute;
  bottom: 20px;
  right: 20px;
  width: 400px;
  height: 470px;
  box-shadow: 0 0 15px rgba(0, 0, 0, .32);
  background: url(./images/bg.png) no-repeat center / 100% 100%;
  .video-plate-title {
    width: 264px;
    height: 48px;
    line-height: 46px;
    margin-top: 28px;
    background: url(./images/titleBg.png) no-repeat center / 100% 100%;
    color: #fff;
    font-size: 18px;
    text-indent: 58px;
  }
}

.video-plate-group {
  margin-top: 8px;
  .video-selector-container {
    position: relative;
    width: 122px;
    height: 22px;
    margin-left: 20px;
    /* button */
    .video-selector-button {
      cursor: pointer;
      width: 122px;
      height: 22px;
      line-height: 22px;
      background: url(./images/selectorBg.png) no-repeat center / 100% 100%;
      font-size: 18px;
      color: #00fff0;
    }
    /* tooltip */
    .video-selector-tooltip {
      display: none;
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 2;
      transform: translate(-50%, -50%);
      width: 122px;
      height: auto;
      padding: 8px 0;
      border-radius: 4px;
      background: #fff;
      .video-selector-tooltip-item {
        cursor: pointer;
        padding: 4px 0;
        text-indent: 12px;
      }
      .video-selector-tooltip-item:hover {
        background: #2a4975;
        color: #fff;
      }
    }
  }
}

.video-list-container {
  position: relative;
  width: 100%;
  height: 150px;
  margin: 12px auto 0 auto;
  /* 左右控制器 */
  .video-list-control {
    cursor: pointer;
    position: absolute;
    top: 50%;
    transform: translateY(-100%);
    width: 28px;
    height: 28px;
  }
  .video-list-control-left {
    left: 8px;
    background: url(./images/left.png) no-repeat center / 100% 100%;
  }
  .video-list-control-right {
    right: 8px;
    background: url(./images/right.png) no-repeat center / 100% 100%;
  }
  /* 视频列表 */
  .video-list-wrapper {
    position: relative;
    width: 315px;
    height: 150px;
    margin: 0 auto;
    overflow: hidden;
    .video-list-group {
      display: -webkit-box;      
      position: absolute;      
      width: 314px;
      height: 150px;   
      .video-list-item {
        margin-left: 10px !important;
        .video-list-item-view {
          position: relative;
          cursor: pointer;
          width: 150px;
          height: 110px;
          border: 1px solid #5f97fb;
          overflow: hidden;
          img {
            width: 150px;
            height: 110px;
            transition: all .5s ease 0s;
          }
          img:hover {
            transform: scale(1.2);
          }
          p {
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 16px;
            line-height: 16px;
            background: rgba(12, 32, 47, .7);
            font-size: 12px;
            color: #fff;
          }
        }
      } 
      .video-list-item:nth-child(1) {
        margin-left: 0;
      }
      .video-list-item-label {
        font-size: 18px;
        color: #5dc1ff;
        margin: 8px 0 0 4px;
      }
    }
  }
}


