.aside-tab-container {
  position: absolute;
  top: 106px;
  right: 20px;
  width: 400px;
  height: 38px;
  box-shadow: 0 0 15px rgba(0, 0, 0, .35);
  background: url(./images/bg.png) no-repeat center / 100% 100%;
}

.aside-tab-group {
  display: flex;
  /* normal */
  .aside-tab-item {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 33.3333%;
    height: 38px;
    .aside-tab-icon {
      position: relative;
      width: 18px;
      height: 18px;
      span {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 100%;
      }
      span:nth-child(1) {
        width: 16px;
        height: 16px;        
        border: 1px solid #5887ef;
      }
    }
    .aside-tab-label {
      margin: 5px 0 0 6px;
      font-size: 18px;
      color: #5887ef;
    }
  }
  /* emphasis */
  .emphasis.aside-tab-item {
    .aside-tab-icon {
      span:nth-child(2) {
        width: 10px;
        height: 10px;
        box-shadow: 0 0 12px rgba(10, 252, 85, 1);
        background: #0afc55;
      }
    }
    .aside-tab-label {
      color: #fff;
    }
  }
}


