header.header-container {
  position: relative;
  display: flex;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 78px;
  background: url(./images/bg.jpg) no-repeat center / 100% 100%;
  .header-logo {
    width: 346px;
    height: 78px;
    background: url(./images/logo.png) no-repeat center / 100% 100%;
  }
}

.header-tab {
  display: flex;
  align-items: center;
  /* normal */  
  .header-tab-item {
    display: flex;
    cursor: pointer;
    margin-left: 50px;
    a {
      color: #6787ac;
      font-size: 18px;
      text-decoration: none;;
    }
    .header-tab-icon {
      position: relative;
      width: 24px;
      height: 24px;
      span {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 100%;
      }
      span:nth-child(1) {
        width: 8px;
        height: 8px;
        border: 1px solid #0b3453;
        background: #11476a;
        z-index: 3;
      }
      span:nth-child(2) {
        width: 16px;
        height: 16px;
        border: 1px solid #0b3453;
        background: #2cd1ef;
        z-index: 2;
      }
      span:nth-child(3) {
        width: 24px;
        height: 24px;
        background: #104468;
        z-index: 1;
        box-shadow: 0 0 15px rgba(72, 199, 246, .48);
      }
    }
    .header-tab-label {
      font-size: 18px;
      font-weight: bold;
      color: #6787ac;
      margin: 5px 0 0 12px;
    }
  }
  /* emphasis */
  .emphasis.header-tab-item {
    .header-tab-icon {
      span:nth-child(1) {
        background: #015f1a;
        border: 1px solid #014513;
      }
      span:nth-child(2) {
        background: #01fd43;
        border: 1px solid #014513;
      }
      span:nth-child(3) {
        background: #015a18;
        box-shadow: 0 0 24px rgba(0, 255, 66, .52);
      }
    }
    .header-tab-label {
      color: #fff;
    }
  }
}

.header-timer {
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
  text-align: center;
  .timer-hour {
    font-size: 24px;
    color: #00f6ff;
  }
  .timer-date {
    margin-top: 4px;
    font-size: 18px;
    color: #bff1ff;
  }
}


