.total{
  svg{
    float: left;
  }
  .content{
    position: relative;
    float: left;
    box-sizing: border-box;
    margin-left: 20px;
    padding-left: 20px;
    height: 100%;
    &:before{
      content: '';
      position: absolute;
      left: 0px;
      top: 18px;
      height: 68px;
      width: 12px;
      background: url(../images/up.png) no-repeat 0 0/100% 100%;
    }
    .value-container{
      height: 50px;
      color: #00ff00;
      font-size: 48px;
      line-height: 50px;
      text-shadow: 0 0 20px #00ff00;
    }
    .unit{
      margin-left: 20px;
      color: #fff;
      font-size: 20px;
    }
    .name{
      height: 50px;
      color: #fff;
      font-size: 20px;
      line-height: 50px;
    }
  }
}


